html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

blockquote {
  border-left: 4px solid #ccc;
  margin-left: 10px;
  padding: 10px 20px;
}

.turbo-progress-bar {
  background-color: #ec005a;
}

.error-message {
  background-color: #fff5f7;
  border: 1px solid red;
  border-left-width: 4px;
  border-radius: 6px;
  padding: 10px 15px;
}

.fly-out {
  z-index: 2;
  position: fixed;
  inset: 0;
}

.fly-out__content {
  z-index: 2;
  background-color: #fff;
  padding: 20px;
  animation-name: fly-out-content-enter;
  animation-duration: .2s;
  animation-timing-function: ease-in;
  animation-fill-mode: both;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.fly-out__backdrop {
  color: #fff;
  text-align: right;
  z-index: 1;
  background-color: #000c;
  padding: 20px;
  position: absolute;
  inset: 0;
}

@keyframes fly-out-content-enter {
  from {
    transform: translate3d(-100%, 0, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fly-out-backdrop-enter {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

